import React from 'react'
import { ReactElement } from 'react'

import { SubjectSummary } from '../../../components/subject/SubjectSummary'
import { SubjectContent } from '../../../components/subject/SubjectContent'
import { SubjectTitle } from '../../../components/subject/SubjectTitle'
import { SubjectKeyword } from '../../../components/subject/SubjectKeyword'
import { SubjectIdentifier } from '../../../components/subject/SubjectIdentifier'
import { Subject } from '../../../components/subject/Subject'

export function Light () : ReactElement {
  return (
    <Subject>
      <SubjectIdentifier>items-armors-light</SubjectIdentifier>
      <SubjectTitle>Armures légères</SubjectTitle>
      <SubjectSummary>
      </SubjectSummary>
      <SubjectContent>
        <table className='table-1d table-vertical fill'>
          <thead>
            <tr>
              <th style={{width: '200px'}}>Armure</th>
              <th style={{width: '100px'}}>Prix</th>
              <th style={{width: '100px'}}>Points</th>
              <th style={{width: '50px'}}>Poids</th>
              <th>Effets</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>Armure matelassée</td>
              <td>10.000c</td>
              <td>+1</td>
              <td>2</td>
              <td>
                Endurance réduite dans les environnements chauds. <br/>
                Endurance améliorée dans les environnements froids.
              </td>
            </tr>
            <tr>
              <td>Armure de cuir (souple)</td>
              <td>20.000c</td>
              <td>+1</td>
              <td>1</td>
              <td>
                Endurance réduite dans les environnements froids.
              </td>
            </tr>
            <tr>
              <td>Armure de cuir (rigide)</td>
              <td>40.000c</td>
              <td>+2</td>
              <td>2</td>
              <td>
                Endurance réduite dans les environnements froids. <br/>
                Dégâts des flèches réduit de 1 point pour un minimum de 0.
              </td>
            </tr>
            <tr>
              <td>Armure de cuir (cloutée)</td>
              <td>50.000c</td>
              <td>+2</td>
              <td>3</td>
              <td>
                Endurance réduite dans les environnements froids. <br/>
                Dégâts des flèches réduit de 1 point pour un minimum de 0.
              </td>
            </tr>
            <tr>
              <td>Chemise de mailles</td>
              <td>100.000c</td>
              <td>+4</td>
              <td>4</td>
              <td>
                Endurance réduite dans les environnements froids. <br/>
                Ne peut pas être portée avec une armure intermédiaire. <br/>
                Peut être portée par dessus une autre armure légère. <br/>
                Ne protège que le torse et les bras. <br/>
                Dégâts tranchants, non-lourds, réduit de moitié.
              </td>
            </tr>
          </tbody>
        </table>
      </SubjectContent>
    </Subject>
  )
}
